.container_trainer-all {
  background: radial-gradient(
      at center,
      rgba(0, 0, 0, 0.25) 100%,
      rgba(0, 0, 0, 0) 0%
    ),
    url("../../Assets/1308025.jpg") no-repeat;
  height: 100vh;
  width: 100%;
  background-size: cover;
  font-family: sans-serif;
  background-position: center;
}

.container_trainer img {
  width: 250px;
  height: 300px;
}

.container_trainer-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 3rem;
  width: 90%;
}

.container_trainer,
.container_trainer-header {
  text-align: center;
}

.container_trainer-header {
  padding: 2rem;
}

.container_trainer-header h1 {
  font-size: 2.8rem;
  padding-bottom: 1rem;
}

.container_trainer-wrap {
  display: flex;
  flex-wrap: wrap;
}

.container_overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  opacity: 0.5;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: all 0.4s ease;
}

.container_overlay a {
  border: 1px solid white;
  border-radius: 50%;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s ease-in;
  width: 20px;
  height: 20px;
  color: #fff;
  padding: 5px;
}

.container_overlay a:hover {
  background-color: #ff0000;
  border: 1px solid #ff0000;
  cursor: pointer;
  color: #fff;
}

.container_trainer-icons:hover .container_overlay {
  height: 100%;
}

.container_trainer h2 {
  padding: 1rem 0;
}

@media screen and (max-width: 900px) {
  .container_trainer-all {
    height: 220vh;
  }
}

@media screen and (max-width: 500px) {
  .container_trainer-wrap {
    padding: 0 1rem;
    justify-content: center;
  }
}
